import AdminLogin from './views/adminend/auth/Login.vue'
import UserLogin from './views/userend/auth/Login.vue'
import UserReg from './views/userend/auth/Register.vue'
import UserWrap from './views/userend/Userwrap.vue'
import App from './App.vue'
import Home from './views/userend/Home.vue'
import AboutUs from './views/userend/About.vue'
import Shop from './views/userend/Shop.vue'
import Cart from './views/userend/Cart.vue'
import Checkout from './views/userend/Checkout.vue'
import Account from './views/userend/Account.vue'
import Orders from './components/userend/Account/Orders.vue'
import Profile from './components/userend/Account/Profile.vue'
import ChangePass from './components/userend/Account/ChangePass.vue'


export default[
    {
        // path:'/admin/',
        // component:App,
        // children:[
        //     {
        //         path:'login',
        //         component:Login
        //     },
        //     {
        //         path:'dashboard',
        //         component:Login
        //     }
        // ],
        path:'/',
        component:App,
        children:[
            {
                path:'',
                component:UserWrap,
                children:[
                    {
                        path:'',
                        component:Home
                    }
                    ,
                    {
                        path:'login',
                        component:UserLogin
                    },
                    {
                        path:'register',
                        component:UserReg
                    },
                    {
                        path:'shop',
                        component:Shop
                    },
                    {
                        path:'about',
                        component:AboutUs
                    },
                    {
                        path:'cart',
                        component:Cart,
                        
                        // props:{showNav:true}
                    },
                    {
                        path:'checkout',
                        component:Checkout,
                        
                        // props:{showNav:true}
                    },
                    {
                        path:'account',
                        component:Account,
                        redirect:'/account/profile',
                        children:[
                            {
                            path:'orders',
                            component:Orders
                            },
                            {
                                path:'profile',
                                component:Profile
                            },
                            {
                                path:'change-password',
                                component:ChangePass
                            },
                        ]
                        // props:{showNav:true}
                    }
                ]
            },
            {
                path:'admin',
                component:App,
                children:[
                    {
                        path:'login',
                        component:AdminLogin
                    }
                  
                ]
            }
        ],
        

        
    },
  
]