<template>
  <form @submit.prevent="validate()">
    
        <div class="form-group">
            <label for="cno">Credit Card Number</label>
            <input v-model="creditcardno" type="text" placeholder="16 digit card number XXXXXXXXXXXX" class="form-control" id="cno" ref="cno" >
    <small class="form-error-text"></small>
       
        </div>
                  <div class="form-row">
<div class="form-group col-sm-6">
    <label for="expirydate">Expiry Date</label>
    <input v-model="expirydate" type="month"  placeholder="CVV" ref="expirydate" class="form-control" id="expirydate" >
    <small class="form-error-text"></small>

  </div>
<div class="form-group col-sm-6">
    <label for="cvv">CVV</label>
    <input v-model="cvv" type="text" ref="cvv" placeholder="3 digit security code at back of credit card" class="form-control" id="cvv" >
    <small class="form-error-text"></small>

  </div>
    </div>
                      <div class="form-row">
<div class="form-group col-sm-12">
    <label for="zipcode">Zip Code</label>
    <input v-model="zipcode" type="text" ref="zipcode" placeholder="3 digits Zip code" class="form-control" id="zipcode" >
    <small class="form-error-text"></small>

  </div>
    </div>
    <button class="btn btn-primary" type="submit" >Place Order</button>
  </form>
</template>

<script>
const errorFields=document.getElementsByClassName('form-error-text');
const creditcard_re=/^[0-9]{16}$/;
const cvv_re=/^[0-9]{3}$/;
export default {
    data:function(){
        return{
            creditcardno:null,
            expirydate:null,
            cvv:null,
            zipcode:null
        }
    }
    ,
    methods:{
        
    validate:function(){
        
        let isvalid=false;
        if(this.creditcardno && this.expirydate && this.cvv && this.zipcode){
            isvalid=true;
        }
       for (var i=0;i<errorFields.length;i++){
            errorFields[i].innerHTML='';
        } 
       
       if(!this.creditcardno){
       this.$refs.cno.nextSibling.innerHTML='Empty credit card no';
       }
       else if(!creditcard_re.test(this.creditcardno)){
       this.$refs.cno.nextSibling.innerHTML='Invalid credit card no';

       }
       if(!this.expirydate){
       this.$refs.expirydate.nextSibling.innerHTML='Empty expiry date';
       }
       if(!this.cvv){
           this.$refs.cvv.nextSibling.innerHTML='Empty CVV';
       }
       else if (!cvv_re.test(this.cvv)){
           this.$refs.cvv.nextSibling.innerHTML='Invalid CVV'
       }
       if(!this.zipcode){
           this.$refs.zipcode.nextSibling.innerHTML='Empty Zipcode'
       }
       else if(!cvv_re.test(this.zipcode)){
           this.$refs.zipcode.nextSibling.innerHTML='Invalid Zipcode'

       }
    // console.log(isvalid);
    // this.$emit('onvalid',isvalid);
    }
}
}
</script>

