<template>
 <form @submit.prevent="validate()">
     <div class="form-group">
         <label for="email">Email</label>
         <input type="text" name="email" ref="email" v-model="email" placeholder="Email" class="form-control" id="email">
   <small class="form-error-text"></small>
     </div>
         <div class="form-group">
         <label for="password">Password</label>
         <input type="password" name="password" ref="password" v-model="password" placeholder="Password" class="form-control" id="password">
   <small class="form-error-text"></small>
     
          <br>
            <div class="form-group">
        
         <button class="btn btn-primary">Login</button>
     </div>
     
     </div>
 </form>
</template>

<script>
export default {
data:function(){
    return {
        password:null,
        email:null,
        isvalid:false,
    }
}
,
methods:{
    validate:function(){
         const errorFields=document.getElementsByClassName('form-error-text');
        const Email_re=/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        // const Phone_re=/^[0-9]{8}$/;
         if(this.email && this.password){
           this.isvalid=true;
        }
        for (var i=0;i<errorFields.length;i++){
            errorFields[i].innerHTML='';
        }
        if(!this.email){
             this.$refs.email.nextSibling.innerHTML='Empty Email';
        }
        
        else if(!Email_re.test(this.email)){
            this.isvalid=false;
            this.$refs.email.nextSibling.innerHTML='Invalid Email';
            
        }
        if(!this.password){
             this.$refs.password.nextSibling.innerHTML='Empty Password';
        }
    }
}
}
</script>

