<template>
     <div class="card" style="width: 100%;" >
        <img :src="img" class="card-img-top p-5 same-size-card-imgs" :alt="name">
        <div class="card-body">
            
          <h5 class="card-title"><b>{{name}}</b></h5>
          <p class="card-text">{{price}}$</p>
          <form class="form-inline">
          <a href="#" class="btn btn-primary">Add to cart</a>
          <div class="quantity-nav mx-auto">
          <a class="quantity-btns"  @click="addqauntity()"><i class="fa fa-plus-circle" aria-hidden="true"></i></a>
          <p class="quantity m-0" ><b>{{quan}}</b></p>
          <a class="quantity-btns" @click="subqauntity()"><i class="fa fa-minus-circle" aria-hidden="true"></i></a>
<span v-if="onsale"  class="badge badge-pill badge-danger notify sale-badge-position">Sale</span>
          </div></form>
     
        </div>
      </div>
</template>

<script>
export default {
props:{
    name: String,
    img:String,
    price:Number,
    onsale:Boolean,
    quantity:Number,

},
data:function(){
    return{

    quan:this.quantity

    }
},
 methods:{
    addqauntity:function(){
    //    console.log(prodObj);
        return this.quan++;
        
    },
    subqauntity:function(){
       if(this.quan>1)
        return this.quan--;
    }
    },
}
</script>

