<template>
<div>

    <section class="section-content padding-y">

<!-- ============================ COMPONENT REGISTER   ================================= -->
	<div class="card mx-auto" style="max-width:520px; margin-top:40px;">
      <article class="card-body">
		<header class="mb-4"><h4 class="card-title">Sign up</h4></header>
		<form @submit.prevent="validate()" autocomplete="off">
				<div class="form-row">
					<div class="col form-group">
						<label>First name</label>
					<input v-model="firstname" ref="firstname" type="text" class="form-control" placeholder="Firstname">
					<small class="form-error-text"></small>

					</div> <!-- form-group end.// -->
					<div class="col form-group">
						<label>Last name</label>
					<input v-model="lastname" ref="lastname" type="text" class="form-control" placeholder="Lastname">
					<small class="form-error-text"></small>

					</div> <!-- form-group end.// -->
				</div> <!-- form-row end.// -->
				
				<div class="form-group">
					<label>Email</label>
					<input ref="email" v-model="email" type="text" class="form-control" placeholder="Email">
					<small class="form-error-text"></small>
				</div>
					<div class="form-row">
				<div class="form-group col-sm-12">
    <label for="phone">Phone</label>
    <div class="form-inline flex-nowrap">
        <select v-model="countrycode" ref="countrycode" class="custom-select w-25 mr-1" id="inlineFormCountryCode">
            <option>+968</option>
        </select>
        <input v-model="phone" placeholder="e.g 91234567" type="text" ref="phone" class="form-control w-75" id="phone" >

    </div>
    <small class="form-error-text"></small>
    
  </div>
				</div>
	
	
				<div class="form-row">
					<div class="form-group col-md-6">
						<label>Create password</label>
					<input v-model="password" class="form-control" type="password" ref="password" placeholder="Password">
    <small class="form-error-text"></small>
					
					</div> <!-- form-group end.// --> 
					<div class="form-group col-md-6">
						<label>Repeat password</label>
					<input v-model="repeatpassword" class="form-control" ref="repeatpassword" type="password" placeholder="Repeat Password">
    <small class="form-error-text"></small>
				
					</div> <!-- form-group end.// -->  
				</div>
			<div class="form-group">
			<button type="submit" :disabled='isvalid' class="btn btn-primary btn-block"><i v-if="isvalid" class="fa fa-spinner fa-spin"></i>
						<span v-else>Login</span>  </button>
			</div> <!-- form-group// -->      

			</form>
		</article><!-- card-body.// -->
    </div> <!-- card .// -->
    <p class="text-center mt-4">Have an account? <a href="/login">Log In</a></p>
    <br><br>
<!-- ============================ COMPONENT REGISTER  END.// ================================= -->


</section>
</div>
</template>

<script>

export default {

	data:function(){
		return {
			firstname:null,
			lastname:null,
			email:null,
			phone:null,
			countrycode:'+968',
			password:null,
		repeatpassword:null,
		isvalid:false,
		}
	},
	methods:{
    validate:function(){
        const errorFields=document.getElementsByClassName('form-error-text');
        const Email_re=/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        const Phone_re=/^[0-9]{8}$/;
        // const Name_re=/^[a-zA-Z]{2,}$/;

        

        if(this.firstname && this.lastname && this.email && this.phone  && this.countrycode && this.password && this.repeatpassword){
            this.isvalid=true;
        }
        for (var i=0;i<errorFields.length;i++){
            errorFields[i].innerHTML='';
        }
        if(!this.firstname){
            this.$refs.firstname.nextSibling.innerHTML='Empty First Name';
        }
        // else if(!Name_re.test(this.firstname)){
        //     isvalid=false;
        //     this.$refs.firstname.nextSibling.innerHTML='Invalid First Name';

        // }
        if(!this.lastname){
            this.$refs.lastname.nextSibling.innerHTML='Empty Last Name';
		} 
		if(!this.password){
            this.$refs.password.nextSibling.innerHTML='Empty Password';
		} 
		if(!this.repeatpassword){
			this.$refs.repeatpassword.nextSibling.innerHTML='Empty Repeat Password';
			this.isvalid=false;
			
		} 
		else{
			if(this.repeatpassword!=this.password){
            this.$refs.repeatpassword.nextSibling.innerHTML='Passwords doesnot match';
			this.isvalid=false;
			}
		}
		
        // else if(!Name_re.test(this.lastname)){
        //     isvalid=false;
        //     this.$refs.lastname.nextSibling.innerHTML='Invalid Last Name';

        // }
        if(!this.email){
            this.$refs.email.nextSibling.innerHTML='Empty Email';
        }
        else if(!Email_re.test(this.email)){
			this.$refs.email.nextSibling.innerHTML='Invalid Email';
			this.isvalid=false;
			

        }
        if(!this.phone){
            this.$refs.phone.parentElement.nextSibling.innerHTML='Empty Phone Number';
        }
        else if(!Phone_re.test(this.phone)){
            this.$refs.phone.parentElement.nextSibling.innerHTML='Invalid Phone Number';
			this.isvalid=false;

        }
   
   
}

}
}
</script>

<style>

</style>