<template>
      <form @submit.prevent="validate()">
          <h1>Add Shipping Details</h1>
          <div class="form-row">
<div class="form-group col-sm-6">
    <label for="firstname">First Name</label>
    <input v-model.trim="firstname" @input="firstname=$event.target.value.charAt(0).toUpperCase() + $event.target.value.toLowerCase().slice(1).trim()" type="text" placeholder="e.g John" ref="firstname" class="form-control" id="firstname" >
    <small class="form-error-text"></small>

  </div>
<div class="form-group col-sm-6">
    <label for="lastname">Last Name</label>
    <input v-model.trim="lastname" @input="lastname=$event.target.value.charAt(0).toUpperCase() + $event.target.value.toLowerCase().slice(1).trim()" type="text" ref="lastname" placeholder="e.g Smith" class="form-control" id="lastname" >
    <small class="form-error-text"></small>

  </div>
    </div>
  
            <div class="form-row">
<div class="form-group col-sm-6">
    <label for="email">Email</label>
    <input v-model.trim="email" type="text" placeholder="e.g Example@demo.com" ref="email" class="form-control" id="email" >
    <small class="form-error-text"></small>
  </div>
<div class="form-group col-sm-6">
    <label for="phone">Phone</label>
    <div class="form-inline flex-nowrap">
        <select v-model="countrycode" ref="countrycode" class="custom-select w-25 mr-1" id="inlineFormCountryCode">
            <option>+968</option>
        </select>
        <input v-model="phone" placeholder="e.g 91234567" type="text" ref="phone" class="form-control w-75" id="phone" >

    </div>
    <small class="form-error-text"></small>
    
  </div>
    </div>
  <div class="form-group">
    <label for="address">Complete Address</label>
    <input v-model.trim="address" type="text" placeholder="e.g House #123 , My Road , My Street , Muscat , Oman" class="form-control" ref="address" id="address">
    <small class="form-error-text"></small>

  </div>
  <button type="submit" class="btn btn-primary">Continue</button>

</form>
</template>

<script>

export default {
computed:{
    getCompPhone:function(){
        return this.countrycode+this.phone;
    }
},
    
    data:function(){
        return{
        firstname:null,
        lastname:null,
        email:null,
        phone:null,
        countrycode:'+968',
        address:null,
        }
       
    },
methods:{
    validate:function(){
        const errorFields=document.getElementsByClassName('form-error-text');
        const Email_re=/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        const Phone_re=/^[0-9]{8}$/;
        // const Name_re=/^[a-zA-Z]{2,}$/;

        
        let isvalid=false;

        if(this.firstname && this.lastname && this.email && this.phone && this.address && this.countrycode){
            isvalid=true;
        }
        for (var i=0;i<errorFields.length;i++){
            errorFields[i].innerHTML='';
        }
        if(!this.firstname){
            this.$refs.firstname.nextSibling.innerHTML='Empty First Name';
        }
        // else if(!Name_re.test(this.firstname)){
        //     isvalid=false;
        //     this.$refs.firstname.nextSibling.innerHTML='Invalid First Name';

        // }
        if(!this.lastname){
            this.$refs.lastname.nextSibling.innerHTML='Empty Last Name';
        }
        // else if(!Name_re.test(this.lastname)){
        //     isvalid=false;
        //     this.$refs.lastname.nextSibling.innerHTML='Invalid Last Name';

        // }
        if(!this.email){
            this.$refs.email.nextSibling.innerHTML='Empty Email';
        }
        else if(!Email_re.test(this.email)){
            isvalid=false;
            this.$refs.email.nextSibling.innerHTML='Invalid Email';

        }
        if(!this.phone){
            this.$refs.phone.parentElement.nextSibling.innerHTML='Empty Phone Number';
        }
        else if(!Phone_re.test(this.phone)){
            isvalid=false;
            this.$refs.phone.parentElement.nextSibling.innerHTML='Invalid Phone Number';

        }
        if(!this.address){
            this.$refs.address.nextSibling.innerHTML='Empty Address';
        }
    // console.log(isvalid);
    this.$emit('onvalid',isvalid);
    }
}
// mixins:[validMixins]
}
</script>

<style>

</style>