<template>
<div class="container" style="height:100vh">
		<div class="d-flex justify-content-center h-100">
			
			<div class="user_card">
				<div class="d-flex justify-content-center" style="flex-direction:column">
					<div class="brand_logo_container">
						<img src="../../../assets/img/arshiamartlogo.png" class="brand_logo" alt="Logo">
					</div>
					<div v-if="errors.length" class="alert alert-danger" role="alert" style="margin-top:10px">
						<ul v-for="error in errors">
							<li>{{error}}</li>
						</ul>
					</div>
				</div>
				<div class="d-flex justify-content-center form_container">
					
					<form @submit="checkform">
						<div class="input-group mb-3">
							<div class="input-group-append">
								<span class="input-group-text"><i class="fa fa-envelope-o" aria-hidden="true"></i>
</span>
							</div>
							<input v-model="email" type="text" name="emailtxt" class="form-control input_user"  placeholder="email">
						</div>
						<div class="input-group mb-2">
							<div class="input-group-append">
								<span class="input-group-text"><i class="fa fa-key" aria-hidden="true"></i></span>
							</div>
							<input v-model="password" type="password" name="passwordtxt" class="form-control input_pass" value="" placeholder="password">
						</div>
						<div class="form-group">
							<div class="custom-control custom-checkbox">
								<input type="checkbox" class="custom-control-input" id="customControlInline">
								<label class="custom-control-label" for="customControlInline">Remember me</label>
							</div>
						</div>
							<div class="d-flex justify-content-center mt-3 login_container">
				 	<button :disabled='isloading' type="submit"  name="button" class="btn login_btn">
						   <i v-if="isloading" class="fa fa-spinner fa-spin"></i>
						   <span v-else>Login</span>
					 </button>
					 
				   </div>
					</form>
				</div>
		
				<div class="mt-4">
					
					<div class="d-flex justify-content-center links">
						<a href="#">Forgot your password?</a>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
	data:function(){
		return {
			email:null,
			password:null,
			errors:[],
			isloading:false
		}
	}
	,
        methods :{
		// login:function(){
			
		// 	// let headers={
		// 	// 	'auth-token':process.env.VUE_APP_ACCESS_TOKEN
		// 	// };
		// 	this.$http.post(process.env.VUE_APP_API_HOST+'/admin/login',{
		// 		// headers
		// 	}).then(function(data){
		// 		console.log(data.body);
		// 	});
		// }
		checkform:function(e){
			e.preventDefault();
			this.errors = [];
      if(!this.password) this.errors.push("Password required.");
      if(!this.email) {
        this.errors.push("Email required.");
      } else if(!this.validEmail(this.email)) {
        this.errors.push("Valid email required.");        
      }
      if(!this.errors.length) {
		 this.isloading=true; 
		  return true;
	  }
      
    },
    validEmail:function(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
    }
	}
}

</script>

<style scoped>
	
		.user_card {
			
			height: 400px;
			width: 350px;
			margin-top: auto;
			margin-bottom: auto;
			/* background: #08C25E; */
			position: relative;
			display: flex;
			justify-content: center;
			flex-direction: column;
			padding: 10px;
			/* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
			-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
			-moz-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
			border-radius: 5px;

		}
.brand_logo{
	width: 100%;
}
		.form_container {
			margin-top: 20px;
		}
		.login_btn {
			width: 100%;
			background: #00AFF2 !important;
			color: white !important;
		}
		.login_btn:focus {
			box-shadow: none !important;
			outline: 0px !important;
		}
		.login_container {
			padding: 0 2rem;
		}
		.input-group-text {
			background: #15295B !important;
			color: white !important;
			border: 0 !important;
			border-radius: 0.25rem 0 0 0.25rem !important;
		}
		.input_user,
		.input_pass:focus {
			box-shadow: none !important;
			outline: 0px !important;
		}
		.custom-checkbox .custom-control-input:checked~.custom-control-label::before {
			background-color: #00AFF2 !important;
		}
   
</style>